body {
	margin: 0;
	padding: 0;
	font-family: 'Source Code Pro', monospace;

}

.contact {
	position: absolute;
	top: 20%;
	left: 10%;
	width: 75%;

}

.contact h1 {
	color: white;
	margin-bottom: 100px;
	margin-left: 85px;
	font-size: 50px;
}

.left h2 {
	margin-bottom: 50px;

}

.contact h3 {
	margin-bottom: 10px;
}
.contact h4 {
	margin: 10px;
	color: green ;
	font-size: large;
}




.contact span {
	color: #DAC0A3 ;
}

.contact .conteneur {
	width: 90%;
	display: flex;
	flex-wrap: wrap;
	margin: auto;
}

.contact .conteneur .left {
	width: 40%;
	color: white;
	display: flex;
	flex-direction: column;
	row-gap: 20px;
	font-size: 25px;
}

.mail {
	display: flex;
	margin-top: 15px;
}


.phone {
	display: flex;
	margin-top: 15px;
}

.contactMeSvg {
	margin-top: 20px;
	margin-right: 20px;
	color: #DAC0A3 ;
	animation: bell 2s cubic-bezier(0.455, 0.03, 0.515, 0.955) infinite;

}

.form {
	display: none;
}

.form text {
	display: none;
}



@keyframes bell {

	0%,
	20%,
	40%,
	60%,
	80% {
		transform: translateY(0) rotate(0deg);
	}

	10%,
	30%,
	50%,
	70%,
	90% {
		transform: translateY(-5px) rotate(5deg);
	}

	25%,
	55%,
	85% {
		transform: translateY(-7px) rotate(-5deg);
	}

	45%,
	75% {
		transform: translateY(-10px) rotate(5deg);
	}

	100% {
		transform: translateY(0) rotate(0deg);
	}

	/* 0% {
		top: 0;
		left: 0;
	}



	50% {
		top: 0;
		left: 50%;
	}

	100% {
		top: 0;
		left: 0%;
	} */
}

.contactMe p {
	padding-bottom: 20px;
