	opacity: 0.6;

}

.contact .conteneur .right .input {
	width: 100%;
	display: flex;
	column-gap: 20px;
}

.contact .conteneur #subject {
	border-radius: 30px;
	width: 445px;
	height: 60px;
	border: solid 2px #DAC0A3 ;

}

.contact .conteneur .right .input input {
	border-radius: 30px;
